* {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  border: 0;
  padding: 0;
  margin: 0;
}

input[type="checkbox"] {
  width: 24px;
  height: 24px;
}

input {
  border: 1px solid #e6e6e6;
  padding: 4px 12px;
  border-radius: 8px;
}

button {
  border: none;
  /* background-color: transparent; */
  /* background-color: blue; */
  /* color: white; */
  border-radius: 8px;
  padding: 8px;
  /* padding: 4px 12px; */
  font-weight: bold;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.player {
  width: 300px;
}

.stats {
  width: 400;
  display: flex;
}

.row {
  display: flex;
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding-block: 16px;
  border-block: 1px solid #e6e6e6;
}

.table {
  border-spacing: 8px;
  border-collapse: separate;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  position: relative;
  text-align: center;
}

.file {
  display: none;
}

tr {
  text-align: center;
}

.teams {
  padding-block: 16px;
  display: flex;
  gap: 16px;
}

.team {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.editBlock {
  padding: 16px;
}
